*
{
    font-family: Arial;
    line-height: 1.2;
    font-size:22px;
    user-select: none;
}
body
{
    margin-top: 50px;
}
.btn
{
    margin-bottom: 70px;
}
.line
{
    height: 3px;
    width: 60px;
    background-color: #000;
    margin: auto;
    margin-top: 5px;
    margin-bottom: 5px;
}
.female_name_i
{
    box-shadow: none !important
}
.line2
{
    @extend .line ;
    position: relative;
    .dot
    {
        position: absolute;
        top: -3px;
        left: 24px;
        height: 10px;
        width: 10px;
        border-radius: 50%;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        -ms-border-radius: 50%;
        -o-border-radius: 50%;
        background-color: #000;
}
}
#city_arabia
{
    direction: rtl;
}
.upper
{
    text-transform: uppercase;
}
.bold
{
    font-weight: bold;
}
.capitalize
{
    text-transform: capitalize;
}
.indent
    {
        text-indent: 6rem;
    }
.header
{
    display: flex;
    text-align: center;
    margin-top: 15px;
    .left
    {
        width: 20%;
        font-size: 17px;

        .city
        {
            font-size: 17px;
        }
    }
    .middle
    {
        width: 60%;
        .model
        {
            text-decoration: underline;
        }
    }
    .right
    {
        width: 20%;
    }
}
.intro
{
    height: 200px;
    padding: 5%;
    text-align: center;
}
.row
{
    margin:0
}
.paragraph
{
    margin: 20px 0;
}
ul
{
    margin-left: 40px;
    li 
    {
        max-width: 90%;
        overflow-wrap: anywhere;
    }
}
.btn
{
    margin: 20px;
}
footer
{
    text-align: center;
    line-height: 3;
    width:70%
}